import { useState } from 'react';

const apiUrl = (chatSessionId: string) => `https://orca-app-tml96.ondigitalocean.app/query-stream/${chatSessionId}`;

type UseSendMessageProps = {
  useCaseId: string;
  chatSessionId: string;
};

function pause() {
  return new Promise((resolve: any) =>
    setTimeout(() => {
      resolve();
    }, 1000),
  );
}

export type SendMessageResponse = {
  text: string | null;
  link: string | null;
  image: string | null;
};

const responseTextStartStr = `<Output>{"Response": "`;
const responseLinkStartStr = `", "product_url": "`;
const responseImageStartStr = `", "image_url": "`;

export default function useSendMessage({ useCaseId, chatSessionId }: UseSendMessageProps) {
  const [nextMessageId, setNextMessageId] = useState<number>(0);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [msgRes, setMsgRes] = useState<SendMessageResponse>({
    text: null,
    link: null,
    image: null,
  });

  const mutate = async (message: string, nextId: number) => {
    setNextMessageId(nextId);
    setIsLoading(true);

    let wholeTxt = '',
      responseTxt = '',
      responseLink = '',
      responseImageLink = '';

    const queryParam = { query: message, usecase_id: Number(useCaseId) };

    const response = await fetch(apiUrl(chatSessionId), {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(queryParam),
    }).catch((err: any) => {
      setIsLoading(false);
    });

    const reader = response?.body?.pipeThrough(new TextDecoderStream()).getReader();

    if (!reader) {
      return;
    }

    while (true) {
      const { value, done } = await reader.read();

      if (done) {
        break;
      }

      let dataDone = false;
      const arr = value.split('][');

      // eslint-disable-next-line no-loop-func
      arr.forEach(async (data) => {
        // ignore empty message
        if (data.length === 0) {
          return;
        }

        if (data.startsWith('[')) {
          data = data.substring(1);
        }

        if (data.endsWith(']')) {
          data = data.substring(0, data.length - 1);
        }

        const json = JSON.parse(data);
        const choice = json.choices?.[0] || {};
        const content = choice.delta?.content || '';
        const contentLength = content.length;

        // ignore empty message
        if (!contentLength) {
          return;
        }

        wholeTxt += content;

        // return when stream is completed
        const finishReason = choice.finish_reason;
        if (finishReason === 'stop') {
          dataDone = true;
          return;
        }

        if (!wholeTxt.startsWith('<')) {
          setMsgRes({ ...msgRes, text: wholeTxt });
          await pause();

          return;
        }

        if (wholeTxt.indexOf(responseTextStartStr) === -1) {
          return;
        }

        const appendResTxt = wholeTxt.substring(responseTextStartStr.length, wholeTxt.length);

        // process response product link when the string content contains responseLinkStartStr and not contains responseImageStartStr
        if (appendResTxt.indexOf(responseImageStartStr) === -1 && appendResTxt.indexOf(responseLinkStartStr) > -1) {
          if (!responseLink) {
            setMsgRes({ text: responseTxt, link: responseLink, image: null });
          }

          const endOfResTxtIdx = appendResTxt.indexOf(responseLinkStartStr);
          const startOfResLinkIdx = endOfResTxtIdx + responseLinkStartStr.length;

          responseLink = appendResTxt.substring(startOfResLinkIdx, appendResTxt.length);

          if (responseLink.includes(`"`)) {
            const idOf1 = responseLink.indexOf(`"`);
            responseLink = responseLink.substring(0, idOf1);
          }
        }

        // process response image link when the string content contains responseImageStartStr
        if (appendResTxt.indexOf(responseImageStartStr) > -1) {
          if (!responseImageLink) {
            setMsgRes({ text: responseTxt, link: responseLink, image: responseImageLink });
          }

          const endOfResLinkIdx = appendResTxt.indexOf(responseImageStartStr);
          const startOfResImageIdx = endOfResLinkIdx + responseImageStartStr.length;

          responseImageLink = appendResTxt.substring(startOfResImageIdx, appendResTxt.length);

          if (responseImageLink.includes(`"`)) {
            const idOf1 = responseImageLink.indexOf(`"`);
            responseImageLink = responseImageLink.substring(0, idOf1);
          }
        }

        // stop checking response text when it detects product link or image link
        if (responseLink || responseImageLink) {
          return;
        }

        if (appendResTxt.indexOf(`"`) > -1) {
          const idxOf = appendResTxt.indexOf(`"`);
          responseTxt = appendResTxt.substring(0, idxOf);
        } else {
          responseTxt = appendResTxt;
        }

        if (responseTxt.indexOf(`"`) > -1) {
          return;
        }

        setMsgRes({ ...msgRes, text: responseTxt });
        await pause();
      });

      if (dataDone) {
        break;
      }
    }

    if (responseTxt || responseLink || responseImageLink) {
      setMsgRes({ text: responseTxt, link: responseLink, image: responseImageLink });
    }

    setIsLoading(false);
    responseTxt = '';
    responseImageLink = '';
    responseLink = '';
    wholeTxt = '';
  };

  return {
    nextMessageId,
    isLoading,
    mutate,
    data: msgRes,
    reset: () => {
      setIsLoading(false);
      setMsgRes({ text: null, link: null, image: null });
      setNextMessageId(0);
    },
  };
}
