import { useLayoutEffect, useState } from 'react';

export default function useGenerateBrowserUID() {
  const [browserUUID, setBrowserUUID] = useState<string>('');

  useLayoutEffect(() => {
    if (window && navigator) {
      const navigator_info = window.navigator;
      const screen_info = window.screen;
      let uuid = '';

      uuid += navigator_info.mimeTypes.length;
      uuid += navigator_info.userAgent.replace(/\D+/g, '');
      uuid += navigator_info.plugins.length;
      uuid += screen_info.height || '';
      uuid += screen_info.width || '';
      uuid += screen_info.pixelDepth || '';

      setBrowserUUID(uuid);
    }
  }, []);

  return browserUUID;
}
