import { useQuery } from '@tanstack/react-query';

import useGenerateBrowserUID from './useGenerateBrowserUID';
import useSupabaseClient from './useSupabaseClient';

type useFetchActiveChatSessionProps = {
  useCaseId: string | undefined;
};

export default function useFetchActiveChatSession({ useCaseId }: useFetchActiveChatSessionProps) {
  const deviceUUID = useGenerateBrowserUID();
  const { data: supabase }: any = useSupabaseClient();

  return useQuery({
    queryKey: ['fetchActiveChatSession', deviceUUID, useCaseId],
    queryFn: async () => {
      const { data } = await supabase
        .from('chat_sessions')
        .select()
        .match({ usecase_id: useCaseId, session_id: deviceUUID, active: true });

      return data?.[0] || null;
    },
    enabled: !!useCaseId && !!supabase && !!deviceUUID,
    refetchOnWindowFocus: false,
  });
}
