import { useMutation } from '@tanstack/react-query';

import useGenerateBrowserUID from './useGenerateBrowserUID';
import useSupabaseClient from './useSupabaseClient';

export default function useCreateChatSession() {
  const deviceUUID = useGenerateBrowserUID();
  const { data: supabase }: any = useSupabaseClient();

  return useMutation({
    mutationFn: async (useCaseId: string) => {
      if (!supabase) {
        return;
      }

      const newChatSession = await supabase
        .from('chat_sessions')
        .insert({ usecase_id: useCaseId, session_id: deviceUUID })
        .select('id');

      return newChatSession;
    },
  });
}
